.registrationContainer {
    width: 660px;
    box-sizing: border-box;
}

.registrationContainerBox {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    border: rgb(223, 223, 223) solid 1px;
    border-radius: 5px;
    padding: 48px 20px 36px 20px;
}

.registrationContainerHeader {
    margin: 0 4% 1%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.registrationInlineTitle {
    font-family: roboto, 'Noto Sans Myanmar UI', arial, sans-serif;
    margin-bottom: 2%;
    font-size: 22px;
}

.registrationContainerHeaderText {
    font-family: 'Noto Sans Myanmar UI', arial, sans-serif;
    font-size: 24px;
    line-height: 1.3333;
    font-size: 24px;
    margin-bottom: 5px;
}

.form {
    width: 100%;
}

.formContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.formInput {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.formInputField {
    width: 100%;
    height: 9vh;
    margin: 2%;
}

.regImg {
    width: 60%;
    height: 100%;
    /* margin-top: -88px; */
}

.imgText {
    text-align: center;
    margin: 0%;
    font-family: 'Noto Sans Myanmar UI', arial, sans-serif
}

.footerButtons {
    width: 97%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

@media only screen and (max-width: 600px) {
    .registrationContainerBox {
        border: none;
    }
    .registrationContainer {
        width: 100%;
    }
    .formInput {
        display: flex;
        flex-direction: column;
    }
    .regImg {
        display: none;
    }
    .snackbar {
        display: none;
    }
}